import React, { Component, createRef } from "react";
import "./progress-bar.component.scss";

interface ProgressBarProps {
  leftRectangleWidth: string;
  theme: "light" | "dark"; // Замените на доступные темы
}

interface ProgressBarState {
  leftRectangleWidth: string;
}

class ProgressBarComponent extends Component<ProgressBarProps, ProgressBarState> {
  private leftRectangleRef: React.RefObject<HTMLDivElement>;

  constructor(props: ProgressBarProps) {
    super(props);
    this.state = {
      leftRectangleWidth: props.leftRectangleWidth,
    };
    this.leftRectangleRef = createRef();
  }

  componentDidUpdate(prevProps: ProgressBarProps) {
    if (prevProps.leftRectangleWidth !== this.props.leftRectangleWidth) {
      this.setState({ leftRectangleWidth: this.props.leftRectangleWidth });
    }
  }

  componentDidMount() {
    if (this.leftRectangleRef.current) {
      this.leftRectangleRef.current.style.width = `${this.state.leftRectangleWidth}%`;
    }
  }

  componentDidUpdate() {
    if (this.leftRectangleRef.current) {
      this.leftRectangleRef.current.style.width = `${this.state.leftRectangleWidth}%`;
    }
  }

  render() {
    const { theme } = this.props;
    const { leftRectangleWidth } = this.state;

    const classForLeftRectangle = `progress-bar--left--${theme}`;
    const classForRightRectangle = `progress-bar--right--${theme}`;

    return (
      <div className="progress-bar">
        <div
          ref={this.leftRectangleRef}
          className={`left-rectangle ${classForLeftRectangle}`}
        ></div>
        <div className={`right-rectangle ${classForRightRectangle}`}></div>
      </div>
    );
  }
}

export default ProgressBarComponent;
